import { get, writable, derived } from 'svelte/store'
import subscribeMany from './utils/subscribeMany'

import devtoolsMessageObservable from './devtoolsMessageObservable'
import clearButtonObservable from './clearButtonObservable'
import { currentPath as currentPathStore } from './navigationStore'

const SESSION_STAGE_STORAGE_KEY = 'xdn-devtools-session-stats'

function updateSessionStats(statState, { prefetch, source, ttfb }) {
  const newObject = updateStatsObject(statState, { prefetch, source, ttfb })

  // Prefetch are counted separately
  if (prefetch) {
    newObject.prefetchRequestCount++
    if (source === 'edge') {
      newObject.prefetchHitCount++
    }
  }

  if (source === 'edge') {
    newObject.edgeTTFB.count++
    newObject.edgeTTFB.total += ttfb
  }

  if (source === 'server') {
    newObject.serverTTFB.count++
    newObject.serverTTFB.total += ttfb
  }

  return newObject
}

function updateStatsObject(statState, { source }) {
  return {
    ...statState,
    [source]: statState[source] + 1,
    updatedAt: Date.now(),
  }
}

// Use a function to make sure we don't share the same object
let initialStats = () => ({
  browser: 0,
  edge: 0,
  server: 0,
})

let initialSessionStats = () => ({
  ...initialStats(),
  prefetchRequestCount: 0,
  prefetchHitCount: 0,
  edgeTTFB: {
    count: 0,
    total: 0,
  },
  serverTTFB: {
    count: 0,
    total: 0,
  },
  updatedAt: Date.now(),
})

// Saving session stats is mostly done to keep them accross page transitions,
// but not for a long-term storage. So we only keep them for 15min.
const MINUTES = 60 * 1000
const SAVED_SESSION_MAX_DELAY = 15 * MINUTES
const savedSessionStatsJson = localStorage.getItem(SESSION_STAGE_STORAGE_KEY)
const savedSessionStats = savedSessionStatsJson && JSON.parse(savedSessionStatsJson)
let initialSessionHits = initialSessionStats()

if (
  savedSessionStats &&
  savedSessionStats.updatedAt &&
  savedSessionStats.updatedAt >= Date.now() - SAVED_SESSION_MAX_DELAY
) {
  initialSessionHits = savedSessionStats
}

const sessionHits = writable(initialSessionHits, function start() {
  window.addEventListener('beforeunload', function() {
    const sessionStats = get(sessionHits)

    // If there are session stats, we store them if they are not too old.
    // We don't want an old opened tab to override the storage on close
    if (sessionStats.updatedAt && sessionStats.updatedAt >= Date.now() - SAVED_SESSION_MAX_DELAY) {
      localStorage.setItem(SESSION_STAGE_STORAGE_KEY, JSON.stringify(sessionStats))
    }
  })

  return subscribeMany([
    devtoolsMessageObservable.subscribe(route =>
      sessionHits.update(statsObject => updateSessionStats(statsObject, route))
    ),
    clearButtonObservable.subscribe(target => {
      if (target === 'session') {
        sessionHits.set(initialSessionStats())
      }
    }),
  ])
})

const currentPageHits = writable(initialStats(), () => {
  let currentPath = window.location.pathname

  return subscribeMany([
    devtoolsMessageObservable.subscribe(route =>
      currentPageHits.update(statsObject => updateStatsObject(statsObject, route))
    ),
    clearButtonObservable.subscribe(target => {
      if (target === 'session') {
        currentPageHits.set(initialStats())
      }
    }),
    currentPathStore.subscribe(path => {
      // This is a safegard, as the store will publish current value
      // upon subscription, which we don't want to reset stats
      if (currentPath !== path) {
        currentPageHits.set(initialStats())
        currentPath = path
      }
    }),
  ])
})

const derivePercentages = stats => {
  const statsWithPercents = { ...stats }
  // This is only used internally for now
  delete statsWithPercents.updatedAt

  const totalRequests = stats.browser + stats.edge + stats.server

  for (const tier of ['browser', 'edge', 'server']) {
    statsWithPercents[`${tier}Percent`] =
      stats[tier] && Math.round((stats[tier] * 100) / totalRequests)
  }
  return statsWithPercents
}

const deriveTTFBAverages = stats => {
  const statsWithAverages = { ...stats }
  for (const ttfb of ['edgeTTFB', 'serverTTFB']) {
    statsWithAverages[`${ttfb}Avg`] = stats[ttfb].count
      ? Math.round(stats[ttfb].total / stats[ttfb].count)
      : null
    delete statsWithAverages[ttfb]
  }

  return statsWithAverages
}

export const sessionStats = derived(sessionHits, stats =>
  derivePercentages(deriveTTFBAverages(stats))
)
export const currentPageStats = derived(currentPageHits, stats => derivePercentages(stats))
