  }

  div.tag-current-page {
    border: 0px;
    color: #e3d857;
  }

  div.tag-prefetch {
    opacity: 0.5;
  }

  div.tag-warning {
    border-color: #ca4ad9;
    color: #ca4ad9;
  }

  div.tag-success {
    border-color: #1bb7c6;
    color: #1bb7c6;
  }

  div.tag-json {
    border-color: #1bb7c6;
    color: #1bb7c6;
  }

  div.tag-html {
