import { writable } from 'svelte/store'
import devtoolsMessageObservable from './devtoolsMessageObservable'
import { currentPath } from './navigationStore'
import clearButtonObservable from './clearButtonObservable'
import subscribeMany from './utils/subscribeMany'

export const items = writable([], () =>
  subscribeMany([
    devtoolsMessageObservable.subscribe(route => {
      items.update(itemsState => [
        {
          id: itemsState.length + 1,
          currentPage: route.path === window.location.pathname && route.method === 'GET',
          ...route,
        },
        ...itemsState,
      ])
    }),
    currentPath.subscribe(currentPath => {
      items.update(itemsState =>
        itemsState.map(route => ({
          ...route,
          currentPage: route.path === currentPath && route.method === 'GET',
        }))
      )
    }),
    clearButtonObservable.subscribe(target => {
      if (target === 'session') {
        items.set([])
      }
    }),
  ])
)
