const descriptions = {
  miss: 'Cache miss',
  ok: 'Cached',
  disabled: 'Not cached: edge caching is explicitly disabled for this route',
  'no-max-age': 'Not cached: cache({ edge: { maxAgeSeconds } }) not set in route',
  code: 'Not cached: the status code was >= 400',
  private: 'Not cached: "private" found in the cache-control header.',
  method: 'Not cached: HTTP method was not GET',
  'body-too-big': 'Not cached: request body was more than 8000 bytes.',
  'set-cookie': 'Not cached: the response contained a set-cookie header. ',
  deployment: 'Not cached: deployment in progress',
}

export default function formatCachingStatus(status) {
  return descriptions[status] || status
}
