import { readable, derived } from 'svelte/store'
import { currentPath } from './navigationStore'

// Ignored from coverage as this is really wip
/* istanbul ignore file */

// Devtools DOM nodes are involved in LCP or CLS are usually not more than 6 levels deep from
// root Devtools DOM node: for example the browser cache items.
function isDevtoolsDOMNode(node, maxDepth = 8) {
  if (node.id === 'xdn-devtools-main-div') {
    return true
  }
  if (maxDepth === 0) {
    return false
  }
  if (!node.parentNode) {
    return false
  }

  return isDevtoolsDOMNode(node.parentNode, maxDepth - 1)
}

/**
 * Code from https://developer.mozilla.org/en-US/docs/Web/API/LargestContentfulPaint
 * @param {*} onLCP
 */
function bindLCP(onLCP) {
  try {
    let lcp

    const po = new PerformanceObserver(entryList => {
      const entries = entryList.getEntries()
      // Find last entry that is NOT a devtools own DOM node
      let k = entries.length - 1
      while (k >= 0) {
        const lastEntry = entries[k--]
        // In some cases lastEntry.element is undefined
        if (lastEntry.element && !isDevtoolsDOMNode(lastEntry.element)) {
          // Update `lcp` to the latest value, using `renderTime` if it's available,
          // otherwise using `loadTime`. (Note: `renderTime` may not be available on
          // image elements loaded cross-origin without the `Timing-Allow-Origin` header.)
          lcp = lastEntry.renderTime || lastEntry.loadTime
          onLCP(lcp)
          break
        }
      }
    })

    po.observe({ type: 'largest-contentful-paint', buffered: true })

    return function unbind() {
      po.disconnect()
    }
  } catch (e) {
    // Do nothing if the browser doesn't support this API.
    return function unbind() {}
  }
}

/**
 * Code from https://developer.mozilla.org/en-US/docs/Web/API/LayoutShift
 * @param {*} onCLS
 */
function bindCLS(onCLS) {
  // Catch errors since some browsers throw when using the new `type` option.
  // https://bugs.webkit.org/show_bug.cgi?id=209216
  try {
    let cumulativeLayoutShiftScore = 0

    const po = new PerformanceObserver(list => {
      for (const entry of list.getEntries()) {
        // Only count layout shifts without recent user input.
        if (!entry.hadRecentInput) {
          // Ignore layout shift from Devtools itself
          const firstSourceNode = entry.sources[0].node
          // In some cases entry.sources[0].node is undefined
          if (firstSourceNode && !isDevtoolsDOMNode(firstSourceNode)) {
            cumulativeLayoutShiftScore += entry.value
          }
        }
      }

      onCLS(cumulativeLayoutShiftScore)
    })

    po.observe({ type: 'layout-shift', buffered: true })

    return function unbind() {
      po.disconnect()
    }
  } catch (e) {
    // Do nothing if the browser doesn't support this API.
  }
}

const CLS_ROUNDING = 10000
function roundCls(clsValue) {
  return Math.round(clsValue * CLS_ROUNDING) / CLS_ROUNDING
}

export const cls = readable(null, function start(set) {
  return bindCLS(clsValue => set(roundCls(clsValue)))
})

let clsCurrentPath = window.location.pathname
let clsPreviousPathCLS

export const clsIncrement = derived([currentPath, cls], ([$currentPath, $cls]) => {
  if ($currentPath !== clsCurrentPath) {
    clsPreviousPathCLS = $cls
    clsCurrentPath = $currentPath
  }
  const increment = clsPreviousPathCLS && $cls - clsPreviousPathCLS
  return increment && `+${roundCls(increment)}`
})

export const lcp = readable(null, function start(set) {
  return bindLCP(lcpValue => set(Math.round(lcpValue)))
})
