<script>
  import formatTTL from './formatTTL'
  import formatCachingStatus from './formatCachingStatus'

  export let item

  const servedFrom = {
    edge: 'EDGE CACHE',
    browser: 'BROWSER CACHE',
    server: 'SERVERLESS',
  }

  function getTTL() {
    const ttl = []
    if (item.routeConfig.edgeMaxAgeSeconds) {
      ttl.push('Edge: ' + formatTTL(item.routeConfig.edgeMaxAgeSeconds))
    }
    if (item.routeConfig.maxAgeSeconds) {
      ttl.push('Browser: ' + formatTTL(item.routeConfig.maxAgeSeconds))
    }
    return ttl.join(', ')
  }
</script>

<style>
  /* !Important: read NOTES_ABOUT_STYLE in 000_Main.svelte */
  span.details-title {
    color: lightgrey;
    text-transform: uppercase;
    margin-right: 4px;
    display: block;
  }
  div.details-item:first-child {
    margin-top: 0;
  }
  div.details-item {
    margin-top: 1em;
  }
  pre {
    white-space: pre-wrap;
    display: inline;
  }
</style>

<div class="$.details-item">
  <span class="$.details-title">URL:</span>
  <pre>{item.path}</pre>
</div>
<div class="$.details-item">
  {#if item.source === 'edge-412'}
    <span class="$.details-title">Status:</span><span class={`xdn-devtools-color-server`}>Prefetching
      was unsuccessful because the response was not found in the edge cache.</span>
  {:else}
    <span class="$.details-title">Served From:</span><span class={`xdn-devtools-color-${item.source}`}>{servedFrom[item.source]}</span>
  {/if}
</div>
{#if item.routeConfig}
  <div class="$.details-item">
    <span class="$.details-title">Route:</span>
    <pre>match({item.routeConfig.criteriaPath || item.routeConfig.pattern})</pre>
  </div>
  {#if item.routeConfig.edgeMaxAgeSeconds || item.routeConfig.maxAgeSeconds}
    <div class="$.details-item">
      <span class="$.details-title">Cache TTL:</span>
      <span>{getTTL()}</span>
    </div>
  {/if}
{/if}
{#if !item.routeConfig}
  <div class="$.details-item"><span class="$.details-title">Route:</span>no matching route</div>
{/if}
{#if item.cacheStatus}
  <div class="$.details-item">
    <span class="$.details-title">Edge Cache Status:</span>{formatCachingStatus(item.cacheStatus)}
  </div>
{/if}
