import { writable, get } from 'svelte/store'

const savedOpenState = localStorage.getItem('xdn-devtools-opened')

// First display is opened by default so that the user can see the
// reload or the welcome message
export const open = writable(savedOpenState ? savedOpenState === 'true' : true)

window.addEventListener('beforeunload', function() {
  localStorage.setItem('xdn-devtools-opened', get(open))
})
