const YEARS = 60 * 60 * 24 * 365.0
const DAYS = 60 * 60 * 24.0
const HOURS = 60 * 60.0
const MINUTES = 60

export default function formatTTL(ttl) {
  if (!ttl) {
    return 'no cache'
  }

  const years = ttl / YEARS
  const days = ttl / DAYS
  const hours = ttl / HOURS
  const minutes = ttl / MINUTES

  if (years % 1 === 0) {
    return `${ttl} seconds (${pluralize(years, 'year')})`
  } else if (days % 1 === 0) {
    return `${ttl} seconds (${pluralize(days, 'day')})`
  } else if (hours % 1 === 0) {
    return `${ttl} seconds (${pluralize(hours, 'hour')})`
  } else if (minutes % 1 === 0) {
    return `${ttl} seconds (${pluralize(minutes, 'minute')})`
  } else {
    return `${ttl} seconds`
  }
}

function pluralize(value, text) {
  return `${value} ${value === 1 ? text : `${text}s`}`
}
