import { readable } from 'svelte/store'

// Watch for client-side navigation. Unfortunaly there is no simple browser API for that
// and we need to monkey-patch pushState method.
export const currentPath = readable(window.location.pathname, function start(set) {
  let originalPushState = history.pushState
  history.pushState = function(...args) {
    if (args[2]) {
      const currentPath = args[2]
      set(currentPath)
    }
    originalPushState.apply(history, args)
  }

  return function stop() {
    history.pushState = originalPushState
  }
})
