/**
 * Light version of RxJS's Observable.
 * We don't pull the library to keep this light.
 *
 * It is compatible with Svelte's stores, the main difference being that a store
 * won't publish a value if it is equal to the previous one, whereas this observable
 * publishes every value.
 *
 * It is usefull for example on the clearButtonObservable that can be triggered
 * multiple time with the same value and that should always trigger listeners
 * callbacks
 *
 *
 * ex:
 *
 * const clickObservable = observable((publish) => {
 *  document.addEventListener('click', publish)
 *
 *  return () => document.removeEventListener('click', publish)
 * })
 *
 * clickObservable.subscribe((clickEvent) => {
 *    ...
 * })
 *
 * @param {function} startFn (optional) Function to be run when upon first subscription
 *                                      It can return a stopFn that will run upon last un-subscription
 */
export default function observable(startFn, { buffered = true } = {}) {
    let nextSubscriberId = 1;
    let subscribersById = {};
    let stopFn;
    const bufferedValues = [];
    function publish(value) {
        if (buffered) {
            bufferedValues.push(value);
        }
        for (const cb of Object.values(subscribersById)) {
            cb(value);
        }
    }
    function subscribe(cb) {
        if (!Object.keys(subscribersById).length) {
            // Start the observable on first subscription
            stopFn = startFn && startFn(publish);
        }
        bufferedValues.forEach(value => cb(value));
        const id = nextSubscriberId++;
        subscribersById[id] = cb;
        return function unsubscribe() {
            delete subscribersById[id];
            if (!Object.keys(subscribersById).length) {
                // Stop the observable on last un-subscription
                stopFn && stopFn();
            }
        };
    }
    return {
        subscribe,
        publish,
    };
}
